// Footer styles
.upper-footer-base{
  background-color: $background-color-light;
  height: 100%;
  border-top: 1px solid $secondary-color;
  margin: 0px;
}
.upper-footer-container{
  display: flex;
  justify-content: space-around;
}
@media only screen and (max-width: 769px) {
  .upper-footer-container{
    flex-direction: column;
  }
}

// One layer down from the upper footer
.upper-footer-container div {
  padding-top: 30px;
  width: 100%;
}

// If some styling need to override
.upper-footer-container div.footer-departments{

}
.upper-footer-container div.footer-about-us{

}
.upper-footer-container div.footer-account-info{

}

// Inside the individual components
.upper-footer-container {
  div span {
    text-transform: capitalize;
    font-size: $font-size-capitals;
    font-weight: $bold-font;
    padding-top: 30px;
  }

  div ul li {
    list-style: none;
    margin-left: -40px;
    padding: 5px 5px;
    width: 100%;
  }

  div ul li:hover {
    cursor: pointer;
    border-left: 4px solid $bright-color;
    border-radius: 0px 5px 5px 0px;

    -webkit-box-shadow: $box-shadow-pallet;
    -moz-box-shadow: $box-shadow-pallet;
    box-shadow: $box-shadow-pallet;
  }
}


// Social media Icons
.footer-about-us a#footer-facebook{
  margin-left: -40px;
}

.footer-about-us a#footer-facebook,
.footer-about-us a#footer-twitter,
.footer-about-us a#footer-instagram,
{
  height: 30px;
  width: 30px;
  margin-right: 20px;
  margin-top: 15px;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.footer-about-us a#footer-facebook {
  background-image: url('/static/img/facebook.png');
}
.footer-about-us a#footer-facebook:hover{
  background-image: url('/static/img/facebook_hover.png');
}
.footer-about-us a#footer-twitter {
  background-image: url('/static/img/twitter.png');
}
.footer-about-us a#footer-twitter:hover{
  background-image: url('/static/img/twitter_hover.png');
}
.footer-about-us a#footer-instagram {
  background-image: url('/static/img/instagram.png');
}
.footer-about-us a#footer-instagram:hover{
  background-image: url('/static/img/instagram_hover.png');
}





.lower-footer-base{
  background-color: $secondary-color;
  color: $background-color;
}

.lower-footer-container{
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid $background-color;
  padding: 20px 0px;
}
@media only screen and (max-width: 769px) {
  .lower-footer-container{
    flex-direction: column;
  }
}

.lower-footer-container span{
  font-weight: $bold-font;
}
.lower-footer-container p{
  color: $background-color-light;
  padding-left: 40px;
}

// If some styles need to override
.lower-footer-container div.fast-delivery {

}
.lower-footer-container div.money-back {

}
.lower-footer-container div.customer-support {

}
.lower-footer-container div.online-payment {

}



.fast-delivery div#faster-deliver-icon,
.money-back div#money-back-icon,
.customer-support div#customer-support-icon,
.online-payment div#online-payment-icon {
  height: 30px;
  width: 30px;
  margin-right: 10px;
  display: inline-block;
  margin-bottom: -20px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.fast-delivery div#faster-deliver-icon {
  background-image: url('/static/img/instagram.png');
}
.money-back div#money-back-icon {
  background-image: url('/static/img/instagram.png');
}
.customer-support div#customer-support-icon {
  background-image: url('/static/img/instagram.png');
}
.online-payment div#online-payment-icon {
  background-image: url('/static/img/instagram.png');
}


.lowest-footer{
  padding: 10px 0px;
  font-size: $font-size-small;
}
.lowest-footer span{
  padding-right: 20px;
}

